const taskNine = `#include <iostream>
#include <array>
#include <vector>
#include <algorithm>
#include <sstream>  
#include <wchar.h>
#include <cwchar>
#include <string>
#include "clocale"

bool test(wchar_t c){
    std::string v = "aioue";
    for (int i = 0; i < 7; i++){
        if(c == v[i])
            return true;
    }
    return false;
}

int count(std::string s){ //   
    int amount = 0;

    for (int i = 0; i < s.length(); i++){
        if(test(s[i]))  //     
            amount++;
    }
    return amount;
}

void split(std::string w)
{
    if (w.length() <= 3 || count(w) == 1){ //    3     1 
        std::cout << w << std::endl; //  
        return ;
    }

    for (int i = 0; i < w.length(); i++){ //    
        if (test(w[i])){  //   
            std::cout << w[i]; //
            if (i != w.length()-1 && count(&w[i+1])) //           
                std::cout << "-"; // 
        }
        else
            std::cout << w[i]; //  
    }
    std::cout << std::endl; //  
}




void pars_line(std::string s, bool & cnt){
    if (cnt){ // 
        std::reverse(s.begin(), s.end()); //   
        std::cout << s << std::endl; //   
    }
    else
        split(s);
    cnt = !cnt;

}

int main(void){
    std::string s; //      
    bool cnt = true; //,         
    std::cout << "Fill some words:\n";
    while(1){   
        
        std::cin >> s; //  

        pars_line(s, cnt);        
        if (std::cin.eof())  //    -  
            break;
    }
    return 0;
}`;

export default taskNine;
